html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

h3 {
  margin: 0;
  font-weight: 500;
}

p {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__items {
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__item:not(:last-child) {
  margin-right: 20px;
}

.header__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.header__link:hover, .header__link.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 72px;
  display: grid;
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card__image-block {
  justify-content: center;
  display: flex;
}

.card__image {
  object-fit: contain;
  object-position: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__product-name {
  text-align: start;
  line-height: 18px;
  font-weight: 500px;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  transition: color .3s, transform .3s;
}

.card__product-code {
  color: #616070;
  justify-content: start;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__review {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__review-count {
  text-align: center;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-tag {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

.card:hover .card__product-name {
  color: #34568b;
  cursor: pointer;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

