.stars {
  display: flex;
  flex-direction: row;
  
  &__star {
    margin-right: 4px;
    width: 16px;
    height: 16px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }
}

.stars--4 .stars__star:nth-child(-n + 4) {
  background-image: url(../images/star-active.svg);
  margin-right: 4px;
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
}
